.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 0 16px;
}

.card__image {
  justify-content: center;
  height: 134px;
  margin-top: 32px;
  display: flex;
}

.card__image-content {
  width: 160px;
  height: 134px;
}

.card__info {
  color: #060b35;
  text-align: left;
  margin-top: 40px;
  margin-bottom: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  text-align: left;
  font-size: 10px;
  line-height: 14px;
  font-weight: inherit;
  align-items: center;
  height: 14px;
  margin-top: 4px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: inherit;
  text-align: left;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-block: 16px;
  display: flex;
}

.card__button-buy {
  background-color: inherit;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 100%;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button-buy:hover {
  color: #00acdc;
}

.card__button--hover:hover {
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
  display: flex;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star:first-child {
  margin-left: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.reviews {
  justify-content: space-between;
  margin-top: 16px;
  display: flex;
}

.reviews__review {
  color: #060b35;
  align-items: flex-end;
  margin: 0;
  font-size: 10px;
  display: flex;
}
